@charset "utf-8";

.form{
  display: flex;
}
.form input{
  flex:1;
  /* flex:content; */
  font-size:1.25rem;
  border:none;
  outline:none;
  border-bottom: 1px solid pink;
  /* 버튼의 가로너비를 제외한 나머지 영역을 차지한다 */
}
.create_btn{
  padding:1rem 2rem;
  margin-left:1rem;
  border-radius:3px;
  font-weight: bold;
  border:none;
  color:#fff;
  border:none;
  font-size:1.1rem;
  background: pink;
}
.create_btn:hover{
  background: rgb(182, 175, 176);
}