@charset "utf-8";

.todo_item{
  display:flex;
  align-items: center;
  cursor:pointer;
  line-height:45px;
}
.todo_item:hover{
  background-color: #ccc;
}
.todo_item:hover .remove{opacity: 1;}
.remove{
  opacity: 0;
  font-weight: bold;
  color: rgb(255, 110, 134);
  margin-right: 0.5rem;
  padding:0px 10px;
}
.todo_text{
  flex:1;
}
.checked{
  text-decoration: line-through;
  color:#333;
}
.check_mark{
  font-size:1.5rem;
  line-height: 1rem;
  color:rgb(255, 110, 134);
  font-weight: bold;
  padding:0px 10px;
}